// GENERATE GRID
@include generate_grid();

.inner {
	margin: 0 auto;
	padding: 0 45px 0 60px;
	max-width: 1280px;
	@include clr;
	@include sm {
		padding: 0 20px; } }

.wrap {
	display: flex;
	min-height: 630px;
	height: calc(100vh - 81px);
	@include sm {
		flex-direction: column; } }

.sidebar {
	padding-top: 40px;
	padding-bottom: 44px;
	@include fls(235px);
	flex-direction: column;
	justify-content: space-between;
	display: flex;
	@include sm {
		flex: 0 0 auto;
		max-width: 100%; }
	&__top {}
	&__bottom {
		@include sm {
 } } }			// display: none

.main {
	padding-top: 30px;
	flex-grow: 1;
	display: flex;
	flex-direction: column; }
