.filters-section {
  display: flex;
  align-items: flex-end;
  margin-bottom: 20px;
  &__filters {
    margin-left: auto;
    @include sm {
      margin-left: 0; } }
  &__title {
    margin-right: 30px; }
  .close-mob-filters-btn {
    display: none; } }

.filters {
  display: flex;
  @include sm {
    flex-direction: column;
    width: 100%; }
  &__item {
    padding: 0 30px;
    text-align: left;
    @include sm {
      padding: 10px 0;
      .bootstrap-select.fit-width {
          width: 100% !important; } } } }
